import React, { useState, useEffect } from 'react';
import './App.css';

const GalleryList = (props) => {
    const {list} = props;
    return (
        <div className="gallery-container">
        {list.map((item) => {
          return (
            <div className="details" key={item.flight_number}>
              <div className="details-image">
                <img
                 src={item.links.mission_patch}/>                
              </div>
               <h3> {item.mission_name} #{item.flight_number}</h3>
                <h4>Mission Ids:{" "}</h4>
                <ul>
                  {" "}
                  <li>{item.mission_id}</li>
                </ul>
                <h4>Launch Year:{" "}
                <span >{item.launch_year}</span>
                </h4>
              <h4 >
                Successful Launch:{" "}
                <span>
                  {item.launch_success ? "true" : "false"}
                </span>
              </h4>
              <h4 >
                Successful Landing:{" "}
                <span>
                  {item.rocket.first_stage.cores[0].land_success ? "true" : "false"}
                </span>
              </h4>
            </div>
         )
        })}

      </div>
    );
}

export default GalleryList;