import React, { useState, useEffect } from 'react';
import './App.css';

import { Button } from 'react-bootstrap';

const SideNavFilter = (props) => {
    const {yearsRange, filterList, reformData} = props
    return (
        <div className="filter-btns">
        <h4>Filters</h4>
        <p>Launch Year</p>
        <div>
    
    {
    yearsRange.map((year, i) => <div className="launch_year" key={i}>
        <Button
    
            variant={
                filterList.launch_year ===
                    year.toString()
                    ? "success"
                    : "outline-success"
            }
            onClick={(e) => reformData(
                "launch_year",
                e.target.value
            )} key={i} value={year}>{year}</Button>
    </div>)}
    </div>
      
    
      <div className="success-launch">
        <div className="true-false-btns">
          
        <p>Successful Launch</p>
          <div className="btns">
          <Button variant={
            filterList.launch_success ===
              'true'
              ? "success"
              : "outline-success"
          } onClick={(e) => reformData("launch_success", e.target.value)} value="true">True</Button>
          </div>
          <div className="btns">
          <Button
            variant={
              filterList.launch_success ===
                'false'
                ? "success"
                : "outline-success"
            } onClick={(e) => reformData("launch_success", e.target.value)} value="false">False</Button>
            </div>
        </div>
        <div className="true-false-btns">
        <p>Successful Landing</p>
        <div className="btns">
          <Button variant={
          filterList.land_success ===
              'true'
              ? "success"
              : "outline-success"
          } onClick={(e) => reformData("land_success", e.target.value)} value="true">True</Button>
          </div>
          <div className="btns">
          <Button variant={
            filterList.land_success ===
              'false'
              ? "success"
              : "outline-success"
          } onClick={(e) => reformData("land_success", e.target.value)} value="false">False</Button>
          </div>
        </div>
      </div>
      </div>
    )
}

export default SideNavFilter;