import React, { useState, useEffect } from 'react';
import './App.css';

import { Button } from 'react-bootstrap';
import querystring from 'querystring';
import axios from 'axios';
import FooterContainer from './footer';
import GalleryList from './galleryList';
import SideNavFilter from './sideNavFilter';

const App = () => {
 

  const [filterList, setFilters] = useState({
    limit: 150,
    launch_year: undefined,
    launch_success: undefined,
    land_success: undefined,
  });
  const [list,setList] = useState([])
 

  const apiData = (filter) => {
   
    const url = "https://api.spacexdata.com/v3/launches?limit=150" + querystring.stringify({ ...filter });
    setFilters(filter);
    axios.get(url)
    .then(res => {
      setList(res.data)
    })
 
   
  }

  const reformData = (val, item) => {
      apiData( {
      ...filterList,
      [val]: filterList[val]===item?item =undefined:item,
    });
  }
  useEffect(() => {
    apiData(filterList);

  }, []);
 const yearsRange = Array.from(new Array(15), (x,i) => 2006+i);

  return (
    <div className="App App-container">
      <link rel="stylesheet" href="https://maxcdn.bootstrapcdn.com/bootstrap/3.3.7/css/bootstrap.min.css" />
      <h2>Spacix Launch Programs</h2>
      <SideNavFilter yearsRange={yearsRange} filterList={filterList} reformData={reformData}/>
      <GalleryList list={list}/>
    
        <FooterContainer />
    </div>
  );
}

export default App
